import isEmpty from 'lodash/isEmpty';
import React, { Component } from 'react';
import BaseVisualization from '@splunk/dashboard-visualizations/common/BaseVisualization';
import { getDataSet } from '@splunk/dashboard-visualizations/utils/propUtils';
import { getClickHandler, enableClickHandler } from '@splunk/dashboard-visualizations/utils/eventUtils';
import ReactSingleValueIcon from '@splunk/react-visualizations/SingleValueIcon';
import { parse, dataContract } from '@splunk/visualization-encoding-parsers/SingleValueIconParser';
import RemoteIcon from '@splunk/visualizations-shared/RemoteIcon';
import { isRemoteIcon, isDataURI } from '@splunk/visualizations-shared/iconUtils';
import SingleValueIconApi from './SingleValueIconApi';
import optionsSchema from './optionsSchema';
import editor from './editorConfig';
import { INITIAL_HEIGHT, INITIAL_WIDTH } from '../SingleValue';

class SingleValueIcon extends Component {
    static propTypes = {
        ...BaseVisualization.propTypes,
    };

    static defaultProps = {
        ...BaseVisualization.defaultProps,
        height: INITIAL_HEIGHT,
    };

    static dataContract = dataContract;

    static vizContract = {
        initialDimension: {
            width: INITIAL_WIDTH,
            height: INITIAL_HEIGHT,
        },
    };

    static schema = optionsSchema;

    static editor = editor;

    constructor(props, context) {
        super(props, context);
        this.api = new SingleValueIconApi(this);
    }

    componentDidMount() {
        // send back the viz api
        this.props.visualizationApiRef(this.api);
    }

    componentWillUnmount() {
        this.props.visualizationApiRef(null);
    }

    render() {
        const {
            dataSources,
            options,
            encoding,
            width,
            height,
            style,
            onEventTrigger,
            mode,
            hasEventHandlers,
        } = this.props;
        const dataSet = getDataSet(this.props);
        const { value, deltaValue, graphData, backgroundColor: encodingBackgroundColor } = parse(
            dataSources,
            encoding
        );

        const showTrendIndicator = !Number.isNaN(Number(deltaValue)) && options.showTrendIndicator !== false;
        const backgroundColor = encodingBackgroundColor || options.backgroundColor;
        // Click handler
        const onValueClick = enableClickHandler(hasEventHandlers, mode)
            ? getClickHandler(onEventTrigger, { value, deltaValue })
            : null;

        // Todo: need refactor later after we have proper icon provider.
        // If icon provider is used, get one RemoteIcon to display as icon.
        let IconComponent = null;
        const { icon } = options;
        if (options && icon && (isRemoteIcon(icon) || isDataURI(icon))) {
            IconComponent = (
                <RemoteIcon
                    iconURL={icon}
                    onIconClick={onValueClick}
                    ref={ref => {
                        this.remoteIcon = ref;
                    }}
                />
            );
        }

        return (
            <ReactSingleValueIcon
                data-test="react-single-value-icon"
                width={width}
                height={height}
                style={style}
                // viz props from options (options < encoding)
                {...options}
                // viz props from encoding
                value={value}
                deltaValue={deltaValue}
                graphData={graphData}
                // viz props based on options and encoding
                backgroundColor={backgroundColor}
                showTrendIndicator={showTrendIndicator}
                IconComponent={IconComponent}
                noDataSources={isEmpty(dataSources) || isEmpty(dataSources.primary)}
                noResult={isEmpty(dataSet) || isEmpty(dataSet.fields) || isEmpty(dataSet.columns)}
                // event handler
                onValueClick={onValueClick}
            />
        );
    }
}

export default SingleValueIcon;
